// create by liuyu 2017.08.02
// update 2017.09.27
(function (window) {
    "use strict";
    var version = 'ver1.0.4';
    console.log(version)
    var root = typeof self == 'object' && self.self === self && self ||
        typeof global == 'object' && global.global === global && global ||
        this;
    // 默认分享参数
    var _defaultArgs = {
        title: '给你分享一个万年历红包',
        text: '100%有奖，立刻能用！',
        image: 'https://mobile.51wnl.com/numberology/redpackage/img/hongbao-share.jpg',
    }
    var device = {};
    var _shareArgs = {};
    var ua = window.navigator.userAgent;
    device.weixin = /MicroMessenger/i.test(ua);

    device.wnl = /wnl/i.test(ua);

    root.shareRedPackage = function (params, cb) {
        if (Object.prototype.toString.call(params) === '[object Object]') {
            _shareArgs = params;
            var shareUrl = encodeURIComponent(params.url.replace(/payresult=1/ig, ''));
            var args = {
                goodsId: _shareArgs.goodsId,
                parterId: _shareArgs.parterId,
                orderId: _shareArgs.orderId,
                shareUrl: shareUrl
            }
            _shareArgs.url = 'http://mobile.51wnl.com/numberology/redpackage/redpackage.html?' + stringify(args);
            setElement();

            //初始化加载资源
            appendResource(function () {
                checkIsTips({
                    goodsId: _shareArgs.goodsId,
                    parterId: _shareArgs.parterId,
                    orderId: _shareArgs.orderId
                }).then(function (error, text, xhr) {
                    var data = JSON.parse(text);
                    var isPay = (/payresult=1/ig).test(window.location.href);
                    var isSharePlatform = device.weixin || device.wnl;
                    console.log('是否弹出红包:', data.data)
                    if (data.data && isPay && isSharePlatform) {
                        wx.ready(function () {
                            console.log('wxshare ready')
                            wxShare(params, true);
                            typeof cb === 'function' && cb();
                        });
                        wx.error(function (e) {
                            alert(JSON.stringify(e));
                            console.log(e)
                        })
                        //弹出红包
                        setTimeout(function () {
                            _$('.redpackage-mask').classList.remove('hidden');
                            setTimeout(function () {
                                _$('.redpackage').style.transform = 'scale(1)';
                                _$('.redpackage').classList.add('.tr');
                                _$('.redpackage').style.webkitTransform = 'scale(1)';
                            }, 50)
                        }, 1000);
                    } else {
                        wx.ready(function () {
                            console.log('wxshare ready')
                            wxShare(params, false);
                            typeof cb === 'function' && cb();
                        });
                        wx.error(function (e) {
                            alert(JSON.stringify(e));
                            console.log(e)
                        })
                    }
                });
            });
        } else {
            throw new Error('object not available');
        }
    }

    root.shareCallback = function () {
        _$('.redpackage-mask').classList.add('hidden');
    }

    function _$(element) {
        var el = document.querySelectorAll(element);
        return el.length > 1 ? el : el[0];
    }

    function stringify(obj) {
        var str = '';
        var keys = Object.keys(obj);
        keys.forEach(function (v, k, arr) {
            k < arr.length - 1 ?
                str += v + '=' + obj[v] + '&' :
                str += v + '=' + obj[v]
        });
        return str;
    }

    function setElement() {
        var el = '<div class="redpackage-mask hidden">\
                    <div class="redpackage">\
                        <div class="redpackage-close"></div>\
                        <div class="redpackage-text-1">你和你的朋友都可</div>\
                        <div class="redpackage-text-2">抽取随机金额优惠券</div>\
                        <div class="redpackage-coin"></div>\
                        <div class="redpackage-text-3">100%</div>\
                        <div class="redpackage-text-4">中奖</div>\
                        <div class="redpackage-btn">与好友分享红包</div>\
                    </div>\
                </div>\
			    <div class="showShareMask hidden">\
			        <div class="shareArrow"></div>\
			    </div>'
        _$('body').insertAdjacentHTML('afterbegin', el);

        var redpackage = _$('.redpackage-mask');

        redpackage.addEventListener('touchmove', function (e) {
            e.preventDefault();
        })

        _$('.redpackage-close').addEventListener('click', function () {
            redpackage.classList.add('hidden');
        });

        _$('.redpackage-btn').addEventListener('click', function () {
            if (device.weixin) {
                _$('.showShareMask').classList.remove('hidden');
            } else {
                console.log(!device.weixin && !device.wnl);
                if (!device.weixin && !device.wnl) {
                    return false;
                }
                commitShare({
                    title: _shareArgs.title || _defaultArgs.title,
                    text: _shareArgs.text || _defaultArgs.text,
                    image: _shareArgs.image || _defaultArgs.image,
                    url: _shareArgs.url,
                }, 'weixin');
            }
        });
    }

    function checkIsTips(params) {
        var data = {
            goodsid: params.goodsId,
            parterid: params.parterId,
            orderid: params.orderId
        }
        return promise.post('//order.51wnl.com/api/coupon/CheckIsTips', data);
    }


    function commitShare(args, platform) {
        var params = {
            direct: true,
            callback: 'shareCallback',
            platform: '',
            shareData: {}
        }
        //设置分享参数
        var shareData = {};
        var perload = false;
        if (args.image === undefined || args.image === null || args.image === '' || args.image === 'shot') {
            perload = false;
        } else {
            perload = true;
        }
        shareData[platform] = platform;
        shareData[platform] = {
            //分享标题
            title: args.title,
            //分享文本
            text: args.text,
            //分享链接图标或者图片
            image: args.image,
            //分享链接
            url: args.url,
            //是否预加载
            preload: perload
        };
        params.shareData = shareData;
        params.platform = platform;
        console.log(params);
        try {
            if (window.ylwindow) {
                ylwindow.reportHasShare(true);
                window.location.href = "protocol://share#" + encodeURIComponent(JSON.stringify(params));
            } else {
                window.location.href = "protocol://share#" + encodeURIComponent(JSON.stringify(params));
                return 1;
            }
        } catch (e) {
            // alert(e);
        }
        return 1;
    }

    function wxShare(params, flag) {
        var title, text, imageURL, url;
        if (flag) {
            title = _shareArgs.title || _defaultArgs.title;
            text = _shareArgs.text || _defaultArgs.text;
            imageURL = _shareArgs.image || _defaultArgs.image;
            url = _shareArgs.url;
        } else {
            title = params.wxShareTitle || _defaultArgs.title;
            text = params.wxShareText || _defaultArgs.text;
            imageURL = params.wxShareImage || _defaultArgs.image;
            url = params.wxShareUrl.replace(/payresult=1/ig, '');
        }
        setShareParams(title, text, imageURL, url);
        function setCallback() {
            _$('.showShareMask').classList.add('hidden');
            _$('.redpackage-mask').classList.add('hidden');
            title = params.wxShareTitle || _defaultArgs.title;
            text = params.wxShareText || _defaultArgs.text;
            imageURL = params.wxShareImage || _defaultArgs.image;
            url = params.wxShareUrl.replace(/payresult=1/ig, '');
            setShareParams(title, text, imageURL, url);
        }
        function setShareParams(title, text, imageURL, url) {
            wx.onMenuShareTimeline({
                title: title, // 分享标题
                link: url, // 分享链接
                imgUrl: imageURL, // 分享图标
                success: function () {
                    // 用户确认分享后执行的回调函数
                    setCallback();
                },
                cancel: function () {
                    // 用户取消分享后执行的回调函数
                }
            });
            //获取“分享给朋友”按钮点击状态及自定义分享内容接口
            wx.onMenuShareAppMessage({
                title: title, // 分享标题
                desc: text, // 分享描述
                link: url, // 分享链接
                imgUrl: imageURL, // 分享图标
                success: function () {
                    // 用户确认分享后执行的回调函数
                    setCallback();
                },
                cancel: function () {
                    // 用户取消分享后执行的回调函数
                }
            });
            //获取“分享到QQ”按钮点击状态及自定义分享内容接口
            wx.onMenuShareQQ({
                title: title, // 分享标题
                desc: text, // 分享描述
                link: url, // 分享链接
                imgUrl: imageURL, // 分享图标
                success: function () {
                    // 用户确认分享后执行的回调函数
                    setCallback();
                },
                cancel: function () {
                    // 用户取消分享后执行的回调函数
                }
            });
        }

    }

    function appendResource(cb) {
        var style = document.createElement('style');
        style.type = 'text/css';
        style.appendChild(document.createTextNode(".hidden{display:none}.redpackage-mask{z-index:1000;width:100%;height:100%;position:fixed;}.redpackage{z-index:1001;line-height:1.15;position:fixed;top:20%;left:50%;margin-left:-135px;color:#fff;text-align:center;font-size:17px;width:270px;height:341px;border-radius:20px;background-color:#dc564a;-webkit-box-shadow:0 6px 20px 0 rgba(0, 0, 0, .3);box-shadow:0 6px 20px 0 rgba(0, 0, 0, .3);background:url('data:image/png;base64,') no-repeat center;background-size:cover;-webkit-transform:scale(0.1);transform:scale(0.1);-webkit-transition:all 1s cubic-bezier(.51, .97, .44, 1.3);-o-transition:all 1s cubic-bezier(.51, .97, .44, 1.3);transition:all 1s cubic-bezier(.51, .97, .44, 1.3)}.redpackage-close{width:30px;height:30px;background-color:#dc564a;position:absolute;right:-8px;top:-8px;border-radius:50%;background-image:url('https://mobile.51wnl.com/numberology/redpackage/img/close2.png');background-position:center;background-repeat:no-repeat;background-size:50%}.redpackage-text-1{margin-top:16px}.redpackage-text-2{margin-top:10px}.redpackage-coin{width:80px;height:82px;margin:25px auto 15px;background:url('https://mobile.51wnl.com/numberology/redpackage/img/hongbao-coin@3x.png');background-size:cover}.redpackage-text-3{list-style:initial;font-size:36px;color:#fcdf79;font-weight:700;font-style:italic}.redpackage-text-4{font-weight:700;color:#fcdf79;margin-top:10px;}.redpackage-btn{width:230px;height:44px;border-radius:50px;background-color:#fcdf79;-webkit-box-shadow:inset 0 -3px 0 0 rgba(216, 191, 104, .5);box-shadow:inset 0 -3px 0 0 rgba(216, 191, 104, .5);font-weight:700;line-height:44px;text-align:center;color:#dc564a;margin:24px auto 0}.showShareMask{position:fixed;z-index:102;left:0;top:0;right:0;bottom:0;background-color:rgba(0, 0, 0, .68)}.shareArrow{position:absolute;top:0;right:20px;width:62px;height:102px;background-image:url('https://mobile.51wnl.com/numberology/redpackage/img/share-arrow.png');background-repeat:no-repeat;background-size:62px 102px;z-index:9999}.tr{-webkit-transform:scale(1)!important;transform:scale(1)}"));
        _$('head').appendChild(style);
        var promiseJs = document.createElement('script');
        promiseJs.src = '//mobile.51wnl.com/numberology/redpackage/js/promise.js';
        promiseJs.type = 'text/javascript';
        _$('head').insertAdjacentElement('beforeend', promiseJs);

        promiseJs.onload = function () {
            var weixinJs = document.createElement('script');
            weixinJs.src = 'https://res.wx.qq.com/open/js/jweixin-1.2.0.js';
            weixinJs.type = 'text/javascript';
            _$('head').insertAdjacentElement('beforeend', weixinJs);
            weixinJs.onload = function () {
                promise.get('//b.cqyouloft.com/interface/API/weixinhandler.ashx', {
                    requesturl: location.href
                }).then(function (error, text, xhr) {
                    var result = JSON.parse(text);
                    // console.log(result)
                    wx.config({
                        /**
                         * 开启调试模式,调用的所有api的返回值会在客户端alert出来，
                         * 若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，
                         * 仅在pc端时才会打印。
                         */
                        debug: false,
                        appId: result.appid, // 必填，公众号的唯一标识
                        timestamp: result.timestamp, // 必填，生成签名的时间戳
                        nonceStr: result.nonceStr, // 必填，生成签名的随机串
                        signature: result.signature,// 必填，签名，见附录1
                        jsApiList: [
                            'onMenuShareTimeline',
                            'onMenuShareAppMessage',
                            'onMenuShareQQ',
                            'onMenuShareWeibo'
                        ] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
                    });
                })
                typeof cb === 'function' && cb();
            }
        }

    }
})(window);



